* {
	box-sizing: border-box;
}
body {
	font-family: 'Roboto', sans-serif;
}
.logoFont {
	font-family: 'Righteous', cursive;
}
.contentSize {
	max-width: 1070px;
	justify-self: center;
}

// Media
@media (max-width: 1280px) {
	.contentSize {
		justify-self: stretch;
	}
}
